@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

*, html {
    font-family: 'Poppins', sans-serif;
    border: 0;
    margin: 0;
    padding: 0;
}

.landing {
    background-image: url('https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/ab15e7be-ab9a-4fe2-966e-63d73c3437be/d9sqogs-ebe87a82-2fc3-42a6-a2b3-ca0b571b78bd.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..jG2OdgiFlCw5nnssh-x96j0omqFFozSYNHvQQrUGrZg');
    display: flex;
    flex-direction: column;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    width: 100vw;
    max-width: 100%;
    height: 90vh;
}

.landing h1 {
    font-size: 4rem;
    line-height: 76%;
}

.landing h2 {
    font-size: 3.5rem;
}